import React from 'react'
import { makeStyles, Typography } from '@material-ui/core';
import './engine-slide-item.scss'
import { EngineModelInterface } from '../../../lib/models/Engine';
import clsx from 'clsx';

interface Props {
  engine: EngineModelInterface;

  onClick?: any
}

const EngineSlideItem = ({
  engine,
  ...props
}: Props) => {
  const { imageUrl, credit, name } = engine;

  const classes = useStyles();

  return (
    <div className={clsx(classes.container, 'Custom Engine' !== name ? null : classes.custom)} {...props} style={{
      backgroundImage: `url(${imageUrl})`
    }}>
      <div className={classes.bgOverlay} />

      {!credit?.url ? null : (
        <Typography variant="caption" className={classes.credit}>
          Cover by <a href={credit.url} target="_blank" className="underline">{credit.name || 'User'}</a>.
        </Typography>
      )}
    </div>
  )
}

const public_url = process.env.PUBLIC_URL;

const useStyles = makeStyles(theme => ({
  container: {
    display: 'block',
    backgroundPosition: 'center center',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
    height: '100vh',
    width: '100vw',
    position: 'relative',
  },
  custom: {
    backgroundSize: '50vw auto',
  },

  bgOverlay: {
    position: 'absolute',
    left: 0,
    bottom: 0,
    width: '100%',
    height: '100%',
    background:
      'linear-gradient(to bottom, ' + ([
        'rgba(0,0,0,0) 0%',
        'rgba(0,0,0,0.1) 25%',
        'rgba(0,0,0,0.25) 75%',
        'rgba(0,0,0,0.5) 100%',
      ]).join(', ') + ')',
    pointerEvents: 'none',
  },
  credit: {
    position: 'absolute',
    right: '1rem',
    bottom: '1rem',
    color: '#fff',
    textShadow: '1px 1px 1px rgba(0, 0, 0, 0.25)',
    zIndex: 5,
    opacity: 0.5,
    transition: '250ms ease-in-out opacity',

    '&:hover': {
      opacity: 1,
    },
    [theme.breakpoints.down('md')]: {
      right: 'auto',
      bottom: 'auto',
      left: '1rem',
      top: '4rem',
    }
  },
}));

export default EngineSlideItem
