import { RecursivePartial } from "tone/build/esm/core/util/Interface";
import { MembraneSynthOptions, EnvelopeOptions } from "tone";
import { LOCAL_URL } from "../statics/permalinks";
import { Model } from "../core/Model";
import { EngineInterface, EngineModeType, ExhaustListType } from "../../types/engine";
import { SynthInterface } from "../../types/tone";
import { WikiInterface } from "../../types/wiki";

export interface EngineModelInterface extends InstanceType<typeof EngineModel> { }

export class Engine extends Model<EngineInterface> implements EngineInterface {
  id?: number = 0;

  name: string = '';

  type?: string
  stroke?: 2 | 4
  firingOrder?: Array<number>
  image?: string
  note?: string
  synth?: keyof SynthInterface
  wiki?: WikiInterface
  hasWiki?: boolean

  createdAt?: any
  updatedAt?: any

  credit?: {
    name?: string;
    url: string;
  };
}

export class EngineModel extends Engine {
  constructor(model: EngineInterface) {
    super(model);

    this.init();
  }

  get imageUrl(): string | undefined {
    const { image } = this.attributes;

    if (!image) {
      return undefined;
    }

    return image.indexOf('http') >= 0 ? image : `${LOCAL_URL}/${image}`;
  }
}

Object.assign(window, {
  ENGINEMODEL: EngineModel
});

export const engineModes: EngineModeType = {
  idle: 'Idle',
  throttle: 'Throttle',
};

export const defaultExhaustEnvelope: RecursivePartial<EnvelopeOptions> = {
  attack: 0.001,
  decay: 0.4,
  sustain: 0.01,
  release: 1.4,
  attackCurve: 'exponential'
};

export const defaultExhaustOptions: RecursivePartial<MembraneSynthOptions> = {
  pitchDecay: 0.0125,
  octaves: 10,
  oscillator: {
    type: 'sine'
  },
  envelope: defaultExhaustEnvelope
};

export const exhaustList: ExhaustListType = {
  default: {
    name: 'Default',
    options: {
      type: 'sawtooth',
    }
  },
  bass: {
    name: 'Long Bass',
    note: 'F1',
    options: {
      type: 'sawtooth128',
    }
  },
  minor: {
    name: 'Minor',
    note: 'D2',
    options: {
      type: 'sawtooth320',
    }
  },
};
