import React, { useState, FunctionComponent, ChangeEvent } from 'react';
import { useSelector } from 'react-redux';
import { RootStoreState, useAppDispatch } from '../../../redux';
import { Grid, makeStyles } from '@material-ui/core';
import { ThemeInterface } from '../../../types/ui';
import MainLayout from '../../layouts/MainLayout';
import { Swiper as SwiperCore } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/swiper.scss'
import { default as ENGINES } from '../../components/Engine/engines_next.example.json'
import { EngineModel, EngineModelInterface, exhaustList } from '../../../lib/models/Engine';
import { EngineInterface } from '../../../types/engine';
import { WikiInterface } from '../../../types/wiki';
import EngineSlideItem from '../../components/Engine/EngineSlideItem';
import { useEffect } from 'react';
import CustomEngineDrawer, { CustomEngineState, engineCustom } from '../../components/CustomEngineDrawer';
import EnginePlayer from '../../components/Engine/EnginePlayer';

interface Props {
  // 
}

interface EngineState extends CustomEngineState {
  engines: EngineModelInterface[];
  enginePlay: boolean;
}

type WikiListType = {
  [key: string]: WikiInterface
};

type SwiperListType = {
  [key: string]: SwiperCore
};

const Home: FunctionComponent<Props> = ({
  ...props
}) => {
  const dispatch = useAppDispatch();
  const theme = useSelector<RootStoreState, ThemeInterface>(({ ui }) => ui.theme);
  const classes = useStyles();

  const [engine, setEngine] = useState<EngineState>({
    engines: [] as Array<EngineModelInterface>,
    enginePlay: false,
    engineMode: 'throttle', // 'idle',
    exhaustMenu: false,
    exhaust: 'default',
    custom: new EngineModel(engineCustom),
    isCustom: false,
    customCylinders: 0,
  });

  const [slideIndex, setSlideIndex] = useState<number>(0);
  const [menu, setMenu] = useState<boolean>(false);
  const [tabActive, setTabActive] = useState<number>(0);

  useEffect(() => {
    const engines: EngineModelInterface[] = [];

    for (let x in ENGINES) {
      const engine = new EngineModel(ENGINES[x] as EngineInterface);

      engines.push(engine);
    }

    setEngine(state => ({
      ...state,
      engines
    }));
  }, []);

  const handleSwiper = (key: string, swiperInstance: SwiperCore) => {
    swiper[key] = swiperInstance;
  }

  const handleChangeSlide = () => {
    const { engines } = engine;
    const swiperSlide = swiper['engine'];
    let slideIndex = 0;

    if (swiperSlide) {
      slideIndex = swiperSlide.activeIndex;

      setSlideIndex(slideIndex);
    }

    setEngine(state => ({
      ...state,
      enginePlay: false,
    }));
  }

  const handleGoToSlide = (index: number | 'next' | 'prev') => {
    const { engines } = engine;
    const enginesCount = engines.length;
    let newIndex = slideIndex;

    if ('next' === index) {
      newIndex = newIndex + 1 > enginesCount - 1 ? enginesCount - 1 : newIndex + 1;
    } else if ('prev' === index) {
      newIndex = newIndex - 1 < 0 ? 0 : newIndex - 1;
    } else {
      newIndex = index;
    }

    setSlideIndex(newIndex);

    swiper['engine'] && swiper['engine'].slideToLoop(newIndex);
  }

  const handleToggleMenu = (open?: boolean) => {
    const toggle: boolean = 'undefined' === typeof open ? !menu : open;

    setEngine(state => ({
      ...state,
      enginePlay: false,
    }));

    setMenu(toggle);
  }

  const handleCustomUpdated = (newEngine: CustomEngineState) => {
    const { engines } = engine;
    const { custom, isCustom, engineMode } = newEngine;
    const swiperEngine = swiper['engine'];
    const filteredEngines = engines.filter(item => item.name !== custom.name);

    if (isCustom) {
      filteredEngines.unshift(custom);
    }

    setEngine(state => ({
      ...state,
      ...newEngine,
      engines: filteredEngines,
    }));

    if (swiperEngine) {
      swiperEngine.update();
      isCustom && swiperEngine.slideTo(0);
    }

    setMenu(false);
  };

  const {
    engines,
    engineMode,
    enginePlay,
    exhaust,
  } = engine;

  return (
    <React.Fragment>
      <MainLayout
        className={classes.container}
        maxWidth={false}
        disableGutters
      >
        <EnginePlayer
          engine={engines[slideIndex]}
          exhaust={exhaustList[exhaust]}
          mode={engineMode}
          item
          xs
          onNavigate={(index: any) => handleGoToSlide(index)}
          onToggle={(type: string) => {
            switch (type) {
              case 'list':
                break;
              case 'options':
                handleToggleMenu();
                break;
            }
          }}
        />
      </MainLayout>

      <CustomEngineDrawer
        open={menu}
        settings={{
          engineMode,
        }}
        onClose={() => handleToggleMenu(false)}
        onOpen={() => handleToggleMenu(true)}
        onUpdated={engine => handleCustomUpdated(engine)}
      />

      <Grid container spacing={0} className={classes.bgContainer}>
        <Swiper
          initialSlide={0}
          speed={400}
          loop={false}
          spaceBetween={0}
          slidesPerView={1}
          onSlideChange={() => handleChangeSlide()}
          onSwiper={(swiper) => handleSwiper('engine', swiper)}
        >
          {engine.engines.map((engine: EngineModelInterface, index: number) => (
            <SwiperSlide key={index}>
              <EngineSlideItem engine={engine} />
            </SwiperSlide>
          ))}
        </Swiper>
      </Grid>
    </React.Fragment>
  );
};

const swiper: SwiperListType = {};

const useStyles = makeStyles(theme => ({
  container: {
    position: 'relative',
    zIndex: 10,
  },

  bgContainer: {
    position: 'fixed',
    left: 0,
    top: 0,
    zIndex: 1,
    width: '100vw',
    height: '100vh',
    transition: '250ms ease-in-out left',
  },
}));

export type { EngineState as HomeEngineState };

export default Home;
