import React, { ChangeEvent, FunctionComponent } from 'react';
import {
  ListItem, makeStyles, Container, List, Typography, ListItemText, ListItemSecondaryAction,
  Menu, MenuItem, Grid, Collapse, FormControl, InputLabel, Button, Select, Switch, TextField,
} from '@material-ui/core';
import clsx from 'clsx';
import { defaultExhaustEnvelope, defaultExhaustOptions, EngineModel, EngineModelInterface, engineModes, exhaustList } from '../../lib/models/Engine';
import BottomDrawer, { BottomDrawerProps } from '../../shared/components/BottomDrawer';
import { FiCheck, FiChevronDown } from 'react-icons/fi';
import { EngineInterface, EngineModeType, ExhaustListType } from '../../types/engine';
import { Link } from 'react-router-dom';
import { useRef } from 'react';
import { useState } from 'react';
import { useTheme } from '@material-ui/core';

interface Props extends BottomDrawerProps {
  onUpdated?: (engine: EngineState) => void;
  settings?: Partial<EngineState>;
}

interface EngineState {
  engineMode: keyof EngineModeType;
  exhaustMenu: boolean;
  exhaust: keyof ExhaustListType;
  isCustom: boolean;
  custom: EngineModelInterface;
  customCylinders: number;
}

const CustomEngineDrawer: FunctionComponent<Props> = ({
  onUpdated,
  settings = {},
  ...props
}) => {
  const theme = useTheme();
  const classes = useStyles();

  const exhaustChevronEl = useRef(null);

  const [engine, setEngine] = useState<EngineState>({
    engineMode: settings.engineMode || 'idle',
    exhaustMenu: false,
    exhaust: 'default',
    custom: new EngineModel(engineCustom),
    isCustom: false,
    customCylinders: 0,
  });

  const isCustomValid = (): boolean => {
    const { custom, customCylinders } = engine;

    if (!custom.firingOrder || !customCylinders) {
      return false;
    }

    const validIgnitions = custom.firingOrder.filter(item => {
      return 'number' === typeof item && item <= 720 && item >= 0;
    });

    return validIgnitions.length === customCylinders; // && validIgnitions.reduce((total, item) => item + total) === 720;
  }

  const handleSelectMode = (mode: keyof EngineModeType) => {
    setEngine(state => ({
      ...state,
      engineMode: mode,
    }));
  }

  const handleToggleExhaust = (open?: boolean) => {
    const toggle = 'undefined' === typeof open ? !engine.exhaustMenu : open;

    setEngine(state => ({
      ...state,
      exhaustMenu: toggle
    }));
  }

  /**
   * Handles when selecting exhaust
   * 
   * @param {any} event Any events might be possible
   * @param {ExhaustListType} key Exhaust slug/key 
   */
  const handleSelectExhaust = (event: any, key: keyof ExhaustListType) => {
    setEngine(state => ({
      ...state,
      exhaust: key,
      exhaustMenu: false,
    }));
  }

  const handleChangeCustom = (event: ChangeEvent<HTMLInputElement>) => {
    setEngine(state => ({
      ...state,
      isCustom: event.target.checked
    }));
  }

  const handleToggleCustom = (toggle?: boolean) => {
    setEngine(state => ({
      ...state,
      isCustom: undefined === toggle ? !engine.isCustom : toggle,
    }));
  }

  const handleChangeCustomCylinder = (event: ChangeEvent<{ name?: string, value: any }>) => {
    const { custom } = engine;
    const count = event.target.value;

    custom.type = `${count} Cylinder${count > 1 ? 's' : ''}`;
    custom.firingOrder = Array(count).fill(999);

    setEngine(state => ({
      ...state,
      customCylinders: count,
      custom,
    }));
  }

  const handleChangeCustomTiming = (event: ChangeEvent<HTMLInputElement>, index: number) => {
    const { custom } = engine;
    const { firingOrder } = custom;
    const value = event.target.value || '999';

    if (firingOrder) {
      custom.firingOrder = firingOrder.map((item, i: number) => {
        return index === i ? parseInt(value) : item
      });

      setEngine(state => ({
        ...state,
        custom
      }));
    }
  }

  const handleApplyMenu = () => {
    if (!isCustomValid() && engine.isCustom) {
      return alert("Sorry, the customized engine is not valid");
    }

    'function' === typeof onUpdated && onUpdated(engine);
  }

  const {
    engineMode,
    exhaustMenu,
    exhaust,
    isCustom,
    custom,
    customCylinders,
  } = engine;
  const { firingOrder } = custom;

  return (
    <BottomDrawer
      anchor="bottom"
      disableSwipeToOpen={true}
      {...props}
    >
      <Container>
        <div className="primary-content">
          <Typography className="mb-0" variant="h3">Engine Options</Typography>

          <List className={clsx(classes.optionList, classes.resetGutters)} dense>
            {Object.keys(engineModes).map(key => (
              <ListItem
                key={key}
                role={undefined}
                button
                onClick={() => handleSelectMode(key)}
              >
                <ListItemText className={classes.optionItemText}>
                  <Typography variant="body1">{engineModes[key]}</Typography>
                </ListItemText>

                <ListItemSecondaryAction>
                  {engine.engineMode === key ? (
                    <FiCheck color={theme.palette.primary.main} size={24} />
                  ) : null}
                </ListItemSecondaryAction>
              </ListItem>
            ))}
          </List>
        </div>

        <div className="primary-content mt-3">
          <Typography className="mb-0" variant="h3">Exhaust Sound</Typography>

          <List className={clsx(classes.optionList, classes.resetGutters)} dense>
            <ListItem
              role={undefined}
              button
              onClick={() => handleToggleExhaust()}
            >
              <ListItemText className={classes.optionItemText}>
                <Typography variant="body1">{exhaustList[engine.exhaust].name}</Typography>
              </ListItemText>

              <ListItemSecondaryAction style={{ pointerEvents: 'none' }} ref={exhaustChevronEl}>
                <FiChevronDown size={24} />
              </ListItemSecondaryAction>
            </ListItem>
          </List>

          <Menu
            anchorEl={exhaustChevronEl.current}
            keepMounted
            open={engine.exhaustMenu}
            onClose={() => handleToggleExhaust(false)}
          >
            {Object.keys(exhaustList).map(key => (
              <MenuItem
                key={key}
                selected={exhaust === key}
                onClick={(event: any) => handleSelectExhaust(event, key)}
              >{exhaustList[key].name}</MenuItem>
            ))}
          </Menu>
        </div>

        <div className="primary-content mt-3">
          <Grid container>
            <Grid item xs>
              <Typography
                className="mb-0"
                variant="h3"
                onClick={() => handleToggleCustom()}
              >Custom Engine</Typography>
            </Grid>

            <Grid item xs="auto">
              <Switch
                checked={engine.isCustom}
                onChange={(event: ChangeEvent<HTMLInputElement>) => handleChangeCustom(event)}
                color="primary"
                name="custom_engine"
                inputProps={{ 'aria-label': 'Custom Engine Checkbox' }}
              />
            </Grid>
          </Grid>

          <Collapse in={engine.isCustom}>
            <Container className={clsx(classes.resetGutters, 'pt-1')}>
              <Typography className="mb-3" variant="body2">
                <span className="mr-1">You can customize the engine's ignition timing. Start by selecting the Cylinders Count.</span>

                <Link className="d-inline-block text-link" to="/learn-custom">Learn more</Link>
              </Typography>

              <FormControl variant="outlined" fullWidth>
                <InputLabel id="cylinder-count">Cylinders Count</InputLabel>
                <Select
                  labelId="cylinder-count"
                  id="field_cylinder-count"
                  value={!engine.customCylinders ? '' : engine.customCylinders}
                  onChange={(event: ChangeEvent<{
                    name?: string,
                    value: any
                  }>) => handleChangeCustomCylinder(event)}
                  label="Cylinders Count"
                  MenuProps={{
                    PaperProps: {
                      style: {
                        maxHeight: '100vw',
                      }
                    }
                  }}
                >
                  {([1, 2, 4, 6, 8, 10, 12]).map(item => (
                    <MenuItem key={item} value={item}>{`${item} cylinder${item > 1 ? 's' : ''}`}</MenuItem>
                  ))}
                </Select>
              </FormControl>

              <Collapse in={engine.customCylinders > 0}>
                <Container className="pt-2 mt-1" disableGutters>
                  <Grid container alignItems="flex-end">
                    <Grid item xs>
                      <Typography variant="body1">Ignition Timing</Typography>
                    </Grid>
                    <Grid item xs="auto">
                      <Typography variant="body2" color="textSecondary">within 720° cycle</Typography>
                    </Grid>
                  </Grid>

                  {!firingOrder ? null : [0].map(() => {
                    let cols: number = engine.customCylinders <= 3 ? engine.customCylinders : 4;

                    if (
                      cols === 4
                      && engine.customCylinders % 3 === 0
                      && engine.customCylinders % 4 !== 0
                    ) {
                      cols = 3;
                    }

                    const rows: number = !customCylinders ? 0 : Math.ceil(customCylinders / cols);
                    const offset: number = (cols * rows) - customCylinders;
                    let row: number = 0;
                    let counter: number = 0;

                    return !rows ? null : (
                      Array.from(Array(rows).keys()).map((itemRow, indexRow) => (
                        row++,
                        <Grid key={`row-${itemRow}`} className="pt-3" container spacing={2}>
                          {Array.from(Array(cols - (row === rows ? offset : 0)).keys()).map((itemCol, indexCol) => (
                            counter++,
                            <Grid key={`col-${itemCol}`} item xs>
                              <TextField
                                label={`${numbers[counter - 1]} Cyl.`}
                                type="number"
                                variant="outlined"
                                size="small"
                                fullWidth
                                tabIndex={counter}
                                value={firingOrder[counter - 1] > 720 ? '' : firingOrder[counter - 1]}
                                InputLabelProps={{
                                  shrink: true,
                                }}
                                inputProps={{
                                  min: 0,
                                  max: 720,
                                  step: 5,
                                  placeholder: '0°'
                                }}
                                InputProps={{
                                  onChange: (event: ChangeEvent<HTMLInputElement>) => handleChangeCustomTiming(event, (indexRow * cols) + indexCol)
                                }}
                              />
                            </Grid>
                          ))}
                        </Grid>
                      ))
                    );
                  })}

                  {/* {firingOrder && customCylinders ? (
                    <Grid className="mt-2" container alignItems="center">
                      <Grid item xs>
                        <Typography variant="body2">
                          Completed Cycle
                        </Typography>
                      </Grid>

                      <Grid className="text-right" item xs>
                        <Typography variant="body2">
                          {firingOrder.reduce((total, item) => {
                            return total + (item > 720 ? 0 : item);
                          }, 0)}° of 720°
                        </Typography>
                      </Grid>
                    </Grid>
                  ) : null} */}
                </Container>
              </Collapse>
            </Container>
          </Collapse>
        </div>

        <div className={clsx(classes.optionAction, 'primary-content', 'mt-3')}>
          <Button
            className={classes.optionActionBtn}
            variant="contained"
            color="primary"
            size="large"
            disableElevation
            fullWidth
            onClick={() => handleApplyMenu()}
            disabled={!isCustom ? false : !isCustomValid()}
          >APPLY CHANGES</Button>
        </div>
      </Container>
    </BottomDrawer>
  );
};

const numbers: string[] = [
  '1st', '2nd', '3rd', '4th', '5th', '6th',
  '7th', '8th', '9th', '10th', '11th', '12th',
  '13th', '14th', '15th', '16th',
];

export const engineCustom: EngineInterface = {
  name: "Custom Engine",
  image: "images/EngineTone.svg",
  type: "Custom",
  firingOrder: []
};

const useStyles = makeStyles(theme => ({
  resetGutters: {
    marginLeft: -1 * theme.spacing(2),
    marginRight: -1 * theme.spacing(2),
    width: 'auto'
  },
  optionList: {

    '& > *': {
      marginLeft: -1 * theme.spacing(1),
      marginRight: -1 * theme.spacing(1),
      borderTop: '1px solid ' + theme.palette.grey[300],

      '&:last-child': {
        borderBottom: '1px solid ' + theme.palette.grey[300],
      }
    }
  },
  optionItemText: {
    paddingLeft: theme.spacing(1),
  },
  optionAction: {
    marginLeft: -1 * theme.spacing(3),
    marginRight: -1 * theme.spacing(3),
  },
  optionActionBtn: {
    borderRadius: 0,
  },
  cylinderSelect: {
    width: '100%',
  },
}));

export type { EngineState as CustomEngineState };

export default CustomEngineDrawer;
