import { ThemeOptions } from '@material-ui/core';
import { createSlice, PayloadAction, SliceCaseReducers } from '@reduxjs/toolkit'
import { darkTheme, lightTheme } from '../../lib/styles/themes';
import { UiStoreState } from '../../types/ui';

export const uiReducer = createSlice<UiStoreState, SliceCaseReducers<UiStoreState>, 'ui'>({
  name: 'ui',
  initialState: {
    theme: {
      type: 'light',
      theme: lightTheme,
    },
  },
  reducers: {
    setTheme: (state, action: PayloadAction<'dark' | 'light'>) => {
      return {
        ...state,
        theme: {
          type: action.payload,
          theme: action.payload === 'dark' ? darkTheme : lightTheme,
        }
      }
    },
  },
});

// Action creators are generated for each case reducer function
export const { setTheme: setUiTheme } = uiReducer.actions;

export default uiReducer.reducer;
