export class Model<A> {
  attributes: A;

  constructor($model: A) {
    this.attributes = {
      ...$model
    };
  }

  init() {
    for (let x in this.attributes) {
      Object.defineProperty(this, x, {
        get: () => this.attributes[x as keyof A],
        set: (value: any) => (this.attributes[x] = value),
      });
    }
  }
}
