import React, { ReactNode, FunctionComponent } from 'react';
import { Container, ContainerProps, Drawer, Grid, IconButton, List, ListItem, ListItemIcon, ListItemText, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import { useState } from 'react';
import { FiMenu, FiSettings, FiInfo, FiStar, FiSend, FiBookmark, FiMoon, FiSun } from 'react-icons/fi';
import { useDispatch, useSelector } from 'react-redux';
import { RootStoreState, setUiTheme } from '../../redux';
import { UiStoreState } from '../../types/ui';

interface Props extends ContainerProps {
  header?: boolean | ReactNode;
  drawer?: boolean | ReactNode;
}

const MainLayout: FunctionComponent<Props> = ({
  children,
  className,
  header = true,
  drawer = true,
  ...props
}) => {
  // Hooks
  const dispatch = useDispatch();
  const classes = useStyles();

  const ui = useSelector<RootStoreState, UiStoreState>(({ ui }) => ui);

  // State
  const [menu, setMenu] = useState<boolean>(false);

  const handleMenuToggle = (open?: boolean) => {
    const toggle: boolean = 'undefined' === typeof open ? !menu : open;

    setMenu(toggle);
  }

  return (
    <React.Fragment>
      <Container maxWidth={false} className={clsx({ [classes.rootOpen]: menu }, 'pointer-events-none', className)} {...props}>
        <Grid
          className={clsx(classes.wrapper, 'pointer-events-none', {
            [classes.wrapperOpen]: menu,
          })}
          container
          direction="column"
        >
          <div className={clsx('pointer-events-auto', classes.overlay)} onClick={() => handleMenuToggle(false)}></div>

          {header === false ? null : (
            'boolean' === typeof header ? (
              <Container className="pointer-events-auto px-0">
                <Grid className={classes.header} item xs="auto">
                  <Grid container alignItems="center" spacing={4}>
                    <Grid item xs>
                      <img src={`${public_url}/images/EngineTone.svg`} alt="EngineTone" className={classes.logo} />
                    </Grid>

                    <Grid item xs="auto">
                      <IconButton onClick={() => handleMenuToggle()}>
                        <FiMenu />
                      </IconButton>
                    </Grid>
                  </Grid>
                </Grid>
              </Container>
            ) : (
              header
            )
          )}

          <Grid className={clsx('pointer-events-none', classes.content)} container item xs direction="column">
            {children}
          </Grid>
        </Grid>
      </Container>

      {drawer === false ? null : (
        'boolean' === typeof drawer ? (
          <Drawer
            className={classes.drawer}
            variant="persistent"
            anchor="right"
            open={menu}
            classes={{
              paper: classes.drawerPaper,
            }}
          >
            <Grid className="pt-4 h-100" container direction="column">
              <Grid className="px-3" item container xs="auto" alignItems="center">
                <Grid item xs>
                  <Typography className="pb-1 mb-2" variant="h2">Menu</Typography>
                </Grid>

                <Grid item xs="auto">
                  <IconButton size="small" onClick={() => dispatch(setUiTheme(ui.theme.type === 'dark' ? 'light' : 'dark'))}>
                    {ui.theme.type === 'dark' ? (
                      <FiSun />
                    ) : (
                      <FiMoon />                      
                    )}
                  </IconButton>
                </Grid>
              </Grid>

              {/* <Grid item xs>
                <List dense>
                  <ListItem button>
                    <ListItemIcon>
                      <FiBookmark size={20} />
                    </ListItemIcon>
                    <ListItemText primary="Bookmarks" />
                  </ListItem>
                  <ListItem button>
                    <ListItemIcon>
                      <FiSettings size={20} />
                    </ListItemIcon>
                    <ListItemText primary="Settings" />
                  </ListItem>

                  <ListItem className="mt-3" button>
                    <ListItemIcon>
                      <FiSend size={20} />
                    </ListItemIcon>
                    <ListItemText primary="Send a Feedback" />
                  </ListItem>
                  <ListItem button>
                    <ListItemIcon>
                      <FiStar size={20} />
                    </ListItemIcon>
                    <ListItemText primary="Rate Us" />
                  </ListItem>
                </List>
              </Grid> */}

              <Grid item xs="auto">
                <List dense>
                  <ListItem button>
                    <ListItemIcon>
                      <FiInfo size={20} />
                    </ListItemIcon>
                    <ListItemText>
                      <Grid container alignItems="center">
                        <Grid item xs>
                          <Typography variant="body1">About</Typography>
                        </Grid>

                        <Grid item xs="auto">
                          <Typography variant="body2" style={{ opacity: .5 }}>v0.1.0</Typography>
                        </Grid>
                      </Grid>
                    </ListItemText>
                  </ListItem>
                </List>
              </Grid>
            </Grid>
          </Drawer>
        ) : (
          drawer
        )
      )}
    </React.Fragment>
  );
};

const public_url = process.env.PUBLIC_URL;

const drawerWidth = 260;

const useStyles = makeStyles(theme => ({
  root: {},
  rootOpen: {
    overflow: 'hidden',
  },
  wrapper: {
    minHeight: '100vh',
    position: 'relative',
  },
  wrapperOpen: {

    '& > $overlay': {
      opacity: 1,
      visibility: 'visible'
    }
  },
  overlay: {
    position: 'fixed',
    left: 0,
    top: 0,
    backgroundColor: 'rgba(0, 0, 0, .2)',
    width: '100%',
    height: '100%',
    zIndex: 500,
    opacity: 0,
    visibility: 'hidden',
    transition: theme.transitions.create(['opacity', 'visibility'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  header: {
    padding: theme.spacing(2),
    paddingBottom: theme.spacing(1),
  },
  logo: {
    height: 32,
    width: 'auto'
  },
  content: {
    flexWrap: 'nowrap',
    flexGrow: 1,
    overflow: 'auto',
  },

  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
}));

export default MainLayout;
