import { ThemeOptions } from "@material-ui/core";

const palette = {
  primary: '#DA1F26',
  secondary: '#223B23',
}

const overrideMuiButton = {
  '&:hover': {
    backgroundColor: 'rgba(255, 255, 255, .12)',
    '@media (hover: none)': {
      backgroundColor: 'transparent',
    }
  }
}

const fontHeading = {
  fontFamily: 'Blinker, sans-serif',
  fontWeight: 600,
  lineHeight: 1.25,
  letterSpacing: '.05rem',
  marginBottom: '.5rem',
};

const gridNoVerticalSpacing = {
  marginTop: 0,
  marginBottom: 0,

  '& > $item': {
    paddingTop: 0,
    paddingBottom: 0,
  }
};

const coreTheme: ThemeOptions = {
  mixins: {
    toolbar: {
      '@media (min-width:600px)': {
        minHeight: 98
      },
      getHeight: (breakpoint: string) => {
        const breakpoints: any = {
          xs: 48, sm: 48,
          md: 56, lg: 56,
          xl: 56,
        }

        return breakpoints[breakpoint] || 48;
      }
    }
  },
  palette: {
    primary: {
      main: palette.primary, // '#212121',
    },
    secondary: {
      main: palette.secondary,
    },
    type: 'light',
  },
  props: {
    MuiButtonBase: {
      // disableRipple: true,
    }
  },
  typography: {
    // useNextVariants: true,
    fontFamily: 'Titillium Web, sans-serif',
    fontSize: 14,
    htmlFontSize: 14,
    h1: { fontSize: '1.75rem', ...fontHeading },
    h2: { fontSize: '1.625rem', ...fontHeading },
    h3: { fontSize: '1.5rem', ...fontHeading },
    h4: { fontSize: '1.375rem', ...fontHeading },
    h5: { fontSize: '1.25rem', ...fontHeading },
    h6: { fontSize: '1.125rem', ...fontHeading },
    subtitle1: { ...fontHeading },
    subtitle2: { ...fontHeading },
    body1: {
      lineHeight: 1.375,
    },
    body2: {
      lineHeight: 1.25
    }
  },
  overrides: {
    MuiGrid: {
      "spacing-xs-1": gridNoVerticalSpacing,
      "spacing-xs-2": gridNoVerticalSpacing,
      "spacing-xs-3": gridNoVerticalSpacing,
      "spacing-xs-4": gridNoVerticalSpacing,
      "spacing-xs-5": gridNoVerticalSpacing,
      "spacing-xs-6": gridNoVerticalSpacing,
      "spacing-xs-7": gridNoVerticalSpacing,
      "spacing-xs-8": gridNoVerticalSpacing,
      "spacing-xs-9": gridNoVerticalSpacing,
      "spacing-xs-10": gridNoVerticalSpacing,
    },
    MuiButton: {
      root: {
        textTransform: 'none',
        lineHeight: 1.5
      },
      text: {
        ...overrideMuiButton,
      },
      outlined: {
        ...overrideMuiButton
      },
      containedPrimary: {
        '&:hover': {
          backgroundColor: '#212121',
        }
      },
    },
    MuiIconButton: {
      root: {
        ...overrideMuiButton
      }
    },
    MuiListItemIcon: {
      root: {
        minWidth: '2rem',
      },
    },
    MuiFormControl: {
      marginDense: {
        marginTop: 0,
        marginBottom: 0,
      }
    },
    MuiInput: {
      underline: {
        '&:after': {
          opacity: 0,
          transform: 'scaleX(1)',
        },
      },
      root: {
        '&$focused': {
          '&:after': {
            opacity: 1,
          }
        },
      },
    },
    MuiTabs: {
      root: {
        minHeight: 0,
      }
    },
    MuiTab: {
      root: {
        textTransform: 'none',
        minHeight: 0,
      }
    },
  },
};

export default coreTheme;
