import * as LINKS from './permalink_list.json';

const debug = process.env.REACT_APP_DEBUG || false;

// Assets
const public_url = process.env.REACT_APP_PUBLIC_URL;
const public_debug_url = process.env.REACT_APP_PUBLIC_DEBUG_URL || 'http://192.168.43.7:8000/api';
const local_url = process.env.PUBLIC_URL;

export const PUBLIC_URL = debug ? public_debug_url : public_url;
export const LOCAL_URL = local_url;

// Access
const api_url = process.env.REACT_APP_API_URL;
const api_debug_url = process.env.REACT_APP_API_DEBUG_URL || 'http://192.168.43.7:8000/api';
const Links: LinkType = LINKS;

export const API_BASE_URL = debug ? api_debug_url : api_url;

// Authorization
export const AUTH_SIGNIN = Links.AUTH_SIGNIN;
export const AUTH_SIGNOUT = Links.AUTH_SIGNOUT;
export const AUTH_SIGNUP = Links.AUTH_SIGNUP;
export const AUTH_FORGOT = Links.AUTH_FORGOT;

export const getUrl = (key: keyof LinkType): string => {
  return PUBLIC_URL + Links[key];
}

export const getLocalUrl = (key: keyof LinkType): string => {
  return LOCAL_URL + Links[key];
}

export const getApiUrl = (key: keyof LinkType): string => {
  return API_BASE_URL + Links[key];
}

type LinkType = {
  [key: string]: string;
};
