import React, { useEffect } from 'react';
import { CssBaseline, MuiThemeProvider, ThemeOptions } from '@material-ui/core';
import { FunctionComponent } from 'react';
import { createTheme } from '@material-ui/core';
import { useSelector } from 'react-redux';
import { RootStoreState } from './redux';
import { ThemeInterface } from './types/ui';
import _defaultsDeep from 'lodash/defaultsDeep';
import { coreTheme } from './lib/styles/themes';
import { useState } from 'react';

const ThemeProvider: FunctionComponent = ({
  children
}) => {
  const themeState = useSelector<RootStoreState, ThemeInterface>(({ ui }) => ui.theme);

  // State
  const [theme, setTheme] = useState<ThemeOptions>({});

  useEffect(() => {
    const activeTheme = {};

    _defaultsDeep(activeTheme, themeState.theme, coreTheme);

    setTheme(activeTheme);
  }, [themeState.type]);

  return (
    <MuiThemeProvider theme={createTheme(theme)}>
      <CssBaseline />
      
      {children}
    </MuiThemeProvider>
  );
}

export default ThemeProvider;
