import React, { Component, createRef, RefObject, ChangeEvent } from 'react'
import { connect } from 'react-redux'
import * as Tone from 'tone'
import { Analyser, Part, Loop, MembraneSynth, MetalSynth, ToneOscillatorType, Volume, Signal } from 'tone'
import { default as p5Lib } from 'p5';
import withStyles, { Styles } from '@material-ui/core/styles/withStyles';
import clsx from 'clsx'
import { Button, Container, Grid, GridProps, IconButton, Slider, Theme, Typography } from '@material-ui/core'
import { default as note_part } from './note_hbd.json'
import { SynthInterface, SynthTypesInterface } from '../../../types/tone'
import { EngineInterface, EngineModeType, ExhaustType } from '../../../types/engine'
import { FiArrowLeft, FiArrowRight, FiCheck, FiList, FiMoreVertical, FiPause, FiPlay } from 'react-icons/fi';

type Props = GridProps & {
  // Own
  engine: EngineInterface;
  exhaust?: ExhaustType;
  mode?: keyof EngineModeType;
  onNavigate?: (index: number | 'prev' | 'next') => void;
  onToggle?: (type: 'list' | 'options') => void;

  // Styles
  classes: Record<string, string>;

}

interface State {
  rpm?: number
  play?: boolean
  oscillators?: any[]
  bankEffects?: any[]
}

class EnginePlayer extends Component<Props, State> {
  canvasContainerEl: RefObject<HTMLDivElement>;
  playButtonEl: RefObject<HTMLButtonElement>;

  visualizer?: Analyser;
  p5?: p5Lib;

  uses: 'synth' | 'oscillator' = 'oscillator';

  synths: SynthInterface;
  synthActive?: SynthTypesInterface;
  synthLoop?: Part | Loop;
  synthInterval: number = 1;
  synthCounter: number = 0;
  synthFiringIndex: number = 0;
  synthDefaultType: keyof SynthInterface = 'bass';
  synthDefaultNote: string = 'D1';

  volume: Volume;
  signal: Signal;
  part?: Part | null = null;

  oscType: ToneOscillatorType = 'sawtooth';

  sliderEl: RefObject<any>;

  constructor(props: Props) {
    super(props);

    this.state = {
      rpm: 1000,
      play: false,
      oscillators: [],
      bankEffects: [],
    };

    this.synths = {
      bass: new MembraneSynth().toDestination(),
      metal: new MetalSynth().toDestination(),
    };

    this.volume = new Tone.Volume(-16).toDestination();
    this.signal = new Tone.Signal(10);

    this.canvasContainerEl = createRef();
    this.playButtonEl = createRef();
    this.sliderEl = createRef();
  }

  get isReady(): boolean {
    if (this.canvasContainerEl) {
      return Boolean(this.canvasContainerEl.current);
    }

    return false;
  }

  async componentDidMount() {
    const { p5, uses } = this;

    this.visualizer = new Analyser('waveform', 128);

    window.addEventListener('resize', () => {
      p5 && p5.windowResized();
    }, false);

    await new Promise<void>(resolve => {
      const checking = () => {
        setTimeout(() => {
          const { current: containerEl } = this.canvasContainerEl;

          if (containerEl) {
            const { width, height } = containerEl.getBoundingClientRect();

            (width > 0 && height > 0) ? resolve() : checking();
          }
        }, 100);
      };

      checking();
    }).then(() => {
      this.switchEngine();

      this.drawVisualizer();
    });

    window.addEventListener('blur', () => {
      this.handlePlay(false);
    }, false);
  }

  shouldComponentUpdate(nextProps: Props, nextState: State, nextContext: any) {
    let result = nextContext;

    if (this.isReady) {
      if (nextProps.engine !== this.props.engine) {
        this.switchEngine(nextProps.engine);

        result = false;
      }

      if (nextProps.exhaust !== this.props.exhaust) {
        this.handlePlay(false);

        result = false;
      }

      if (nextProps.mode !== this.props.mode) {
        this.p5 && this.p5.windowResized();

        result = true;
      }
    }

    return result;
  }

  idleFrequency() {
    return 10; //this.rpmToFrequency(1000);
  }

  rpmToFrequency(rpm: number) {
    const cycle = (this.props.engine.stroke || 4) / 2;

    return (rpm / cycle) / 60;
  }

  switchEngine(engine?: EngineInterface) {
    this.buildEngine(engine);
  }

  buildEngine(engine?: EngineInterface) {
    const {
      isReady,
      visualizer,
      signal,
      volume,
    } = this;
    const { oscillators, bankEffects } = this.state;
    const { exhaust } = this.props;
    const {
      firingOrder,
      firingBank,
      accurate,
      rpmIdle,
      bank = 1
    } = engine || this.props.engine;

    const newOscs: any[] = [];
    const newBankEffects: any[] = [];
    const idleFrequency = this.idleFrequency();

    // visualizer && volume.disconnect(visualizer);

    if (!firingOrder || !isReady) {
      return void (this.setState({ oscillators: [] }), 0);
    }

    console.log("LIGS", bankEffects, oscillators)

    this.togglePart(false);

    if (oscillators?.length) {
      oscillators.forEach(({ oscillator, effects = [], bank: bankId }) => {
        const bankEffect = bankEffects?.find(({ bank }) => bank === bankId);

        if (bankEffect) {
          oscillator.disconnect(bankEffect);
        }

        effects.map((item: any) => {
          oscillator.disconnect(item);

          item.dispose();
        });

        signal.disconnect(oscillator.frequency);

        // oscillator.disconnect(volume);

        oscillator.dispose();
      });

      console.log("OSCS", oscillators.length);
    }

    if (bankEffects?.length) {
      bankEffects.forEach(({ effect }) => {
        effect.dispose();

        console.log("DISPOSE BANK EFFECT");
      });
    }

    Array.from(Array(bank)).map((item, i) => {
      const iteration = i + 1;
      const wahStr = iteration * 128;
      const bankEffect = new Tone.AutoWah(wahStr, 8, -40).connect(volume);
      // const bankEffect = new Tone.FrequencyShifter(wahStr).connect(volume);
      // const bankEffect = new Tone.Reverb(10).connect(volume);

      newBankEffects.push({
        bank: i,
        effect: bankEffect,
      });
    });

    firingOrder.forEach((fire, index) => {
      const extraDegrees = [-4, -2, 0, 2, 4]; // [0];
      const randomIndex = Math.floor(Math.random() * extraDegrees.length);

      const fired = accurate ? fire : (fire + extraDegrees[randomIndex]);
      const firedBank = !firingBank ? 0 : firingBank[index];

      const start = fire === 0 ? 0 : ((fired / 720) / 10);

      const bankEffect = newBankEffects.find(({ bank }) => bank === firedBank);
      const effects: any[] = [];

      if (firingBank && firingBank[index] !== undefined) {
        // const chebyStr = (index + 1) % bank === firingBank[index] ? 2 : 12;
        // const cheby = new Tone.BitCrusher(chebyStr).toDestination();
        // effects.push(cheby);

        // const shiftStr = (index + 1) % bank === firingBank[index] ? 1 : 2;
        // const shift = new Tone.FrequencyShifter(shiftStr * (index + 1)).toDestination();
        // effects.push(shift);

        // const wahStr = firedBank ? 4 : 40;
        // const autoWah = new Tone.AutoWah(wahStr + (index * 8), 1, -40).toDestination();
        // effects.push(autoWah);

        // console.log("FIXK", firingBank[index], wahStr);
      }

      const oscillator = new Tone.Oscillator({
        type: 'sawtooth',
        frequency: idleFrequency,
      });

      const oscChains = [];

      if (bankEffect) {
        // oscillator.connect(bankEffect.effect);
        oscChains.push(bankEffect.effect);
      }

      effects.forEach((item) => {
        // oscillator.connect(item);
        oscChains.push(item);
      });

      oscChains.length && oscillator.chain(...oscChains);

      this.visualizer && oscillator.connect(this.visualizer);
      
      signal.connect(oscillator.frequency);

      newOscs.push({
        oscillator,
        effects,
        start,
        ...(!bankEffect ? null : { bank: bankEffect.bank })
      });
    });


    // this.oscillators = [{ oscillator: osc, start: 0 }];
    // this.bankEffects = newBankEffects;
    // this.oscillators = newOscs;
    this.setState({
      oscillators: newOscs,
      bankEffects: newBankEffects,
    })

    // visualizer && merge.connect(visualizer);
    signal.rampTo(idleFrequency, 0);

    this.setState({
      rpm: rpmIdle || 1000,
      play: false,
    });
  }

  drawVisualizer() {
    const {
      visualizer,
      canvasContainerEl: { current: container },
      isReady
    } = this;

    if (!isReady || !container || !visualizer) {
      return;
    }

    let { width, height } = container.getBoundingClientRect();

    container.innerHTML = '';

    const p5 = this.p5 = new p5Lib((p: p5Lib) => {
      p.setup = () => {
        const canvas = p.createCanvas(width, height);

        canvas.parent(container);
        p.background(255, 1);
      };

      p.windowResized = () => {
        const { width: newWidth, height: newHeight } = container.getBoundingClientRect();

        width = newWidth;
        height = newHeight;

        p.resizeCanvas(width, height);
      };

      p.draw = () => {
        const { play } = this.state;
        const dimension = Math.min(width, height);

        // p.background(255, 200);
        p.clear();

        p.strokeWeight(dimension * 0.01);
        p.stroke('#DA1F26');
        p.noFill();

        if (play) {
          const values = visualizer.getValue();

          p.beginShape();

          values.forEach((amplitude: any, i: number) => {
            const x = p.map(i, 0, values.length - 1, 0, width);
            const y = height / 2 + (amplitude / 6) * height;

            p.vertex(x, y);
          });

          p.endShape();
        }
      };
    });
  }

  handleChangeRPM(value: number | number[], ramp: number = 0.01) {
    const { play } = this.state;
    const rpm = 'object' === typeof value ? value[0] : value;

    if (!play) {
      return this.setState({ rpm: 1000 });
    }

    this.setState({ rpm }, () => {
      this.signal.rampTo(this.rpmToFrequency(rpm), ramp);
    });
  }

  handlePlay(play?: boolean) {
    const { exhaust, engine: { rpmIdle } } = this.props;
    const { play: playState, oscillators } = this.state;
    const toggle = 'undefined' === typeof play ? !playState : play;
    const newState: State = { play: toggle };

    this.signal.rampTo(this.idleFrequency(), 0.01);

    if (oscillators?.length) {
      const time = Tone.now();

      oscillators.forEach(({ oscillator, start }) => {
        exhaust?.options && oscillator.set({
          ...exhaust.options
        });

        toggle ? oscillator.start(time + start).syncFrequency() : oscillator.stop();
      });
    }

    if (!toggle) {
      this.togglePart(false);
    }

    this.sliderEl.current?.querySelector('[role="slider"]').focus();

    console.log("SLDIER EL", this.sliderEl.current);

    setTimeout(() => {
      this.setState(newState, () => {
        setTimeout(() => {
          this.handleChangeRPM(rpmIdle || 1000, 0.1);
        }, 250);
      });
    }, 100);
  }

  togglePart(play?: boolean | undefined) {
    const toggle = 'undefined' === typeof play ? !this.part : play;

    if (!toggle) {
      this.part?.stop();
      this.part = null;
      Tone.Transport.stop();

      return void (0);
    }
  }

  handleHbd() {
    const { play } = this.state;

    if (!play) {
      this.handlePlay();
    } else {
      this.togglePart(false);
    }

    this.part = new Tone.Part(((time, part) => {
      this.handleChangeRPM(part.rpm, part.rampDuration || 0.05);
    }), note_part.notes as Engine.NoteItem[]).start();

    Tone.Transport.start();
  }

  handleNavigate = (index: number | 'prev' | 'next') => {
    'function' === typeof this.props.onNavigate && this.props.onNavigate(index);
  }

  handleToggle = (type: 'list' | 'options') => {
    'function' === typeof this.props.onToggle && this.props.onToggle(type);
  };

  render() {
    const {
      mode: engineMode,
      engine,
      exhaust,
      classes,
      ...restProps
    } = this.props;
    const { rpm, play: playState } = this.state;
    const { rpmLimit, name, type } = engine || {};

    return (
      <Grid container direction="column" xs spacing={0} {...restProps}>
        <Grid
          item xs="auto"
          container
          spacing={0}
          justifyContent="center"
          className="mt-6 -mb-6"
        >
          <Button
            variant="outlined"
            color="default"
            onClick={() => this.handleHbd()}
            className="pointer-events-auto ml-3 text-white border-white"
            style={{
              textShadow: '1px 1px 1px rgba(0, 0, 0, .33)'
            }}
          >Happy Birthday</Button>
        </Grid>

        <Grid item xs className={classes.p5container}>
          <div className={classes.p5wrapper} ref={this.canvasContainerEl} />
        </Grid>

        <Grid item xs="auto" className={clsx("-mt-24", classes.playerContainer)}>
          <Container className="pointer-events-auto px-6 py-4" style={{ maxWidth: 420, color: '#fff' }}>
            <div className="primary-content text-center engine-info">
              <Typography variant="h1" className="mb-1">{name}</Typography>
              <Typography variant="body1">{type}</Typography>
            </div>

            <Grid container alignItems="center" className="mt-5">
              <Grid item xs>
                <Grid container alignItems="center">
                  <Typography variant="body2" className="mb-0 mr-2">Rpm</Typography>

                  <Typography variant="h3" className="mb-0">{rpm}</Typography>
                </Grid>
              </Grid>

              <Grid item xs="auto">
                <Typography variant="h6" className="mb-0">{engineMode === 'slider' ? 'RPM Slider' : (
                  engineMode === 'throttle' ? 'Throttle' : 'Idle'
                )}</Typography>
              </Grid>
            </Grid>

            {engineMode !== 'throttle' ? null : (
              <Slider
                ref={this.sliderEl}
                value={rpm}
                min={50}
                max={rpmLimit || 12000}
                step={50}
                aria-labelledby="continuous-slider"
                onChange={(event: ChangeEvent<{}>, value: number | number[]) => this.handleChangeRPM(value)}
              />
            )}

            <Grid container spacing={0} justifyContent="space-between" alignItems="center" className="mt-4 -mx-4 w-auto">
              <Grid item xs="auto">
                <IconButton className={classes.playNavBtnTransparent} onClick={() => this.handleToggle('list')}>
                  <FiList size={28} />
                </IconButton>
              </Grid>

              <Grid item xs="auto">
                <IconButton className={classes.playNavBtn} onClick={() => this.handleNavigate('prev')}>
                  <FiArrowLeft size={28} />
                </IconButton>
              </Grid>

              <Grid item xs="auto">
                <IconButton className={classes.playBtn} onClick={() => this.handlePlay()}>
                  {playState ? (
                    <FiPause size={36} />
                  ) : (
                    <FiPlay size={36} />
                  )}
                </IconButton>
              </Grid>

              <Grid item xs="auto">
                <IconButton className={classes.playNavBtn} onClick={() => this.handleNavigate('next')}>
                  <FiArrowRight size={28} />
                </IconButton>
              </Grid>

              <Grid item xs="auto">
                <IconButton className={classes.playNavBtnTransparent} onClick={() => this.handleToggle('options')}>
                  <FiMoreVertical size={28} />
                </IconButton>
              </Grid>
            </Grid>
          </Container>
        </Grid>
      </Grid>
    )
  }
}

const styles: Styles<Theme, any> = (theme: Theme) => ({
  p5container: {
    width: '100%',
    position: 'relative',
    pointerEvents: 'none',

    '& > canvas': {
      width: '100%',
      height: '100%',
      display: 'block',
    }
  },
  p5wrapper: {
    position: 'absolute',
    top: -1000,
    left: -1000,
    bottom: -1000,
    right: -1000,
    margin: 'auto',
    maxWidth: '100%',
    maxHeight: '100%',
  },

  playerActions: {
    position: 'absolute',
    top: '5rem',
    left: '0',
    zIndex: 50,
    alignItems: 'center'
  },

  throttle: {
    position: 'absolute',
    bottom: 0,
    left: 0,
    width: '100%',
    padding: theme.spacing(0),
  },

  playerContainer: {
    position: 'relative',
    zIndex: 25,
  },
  playBtn: {
    boxShadow: theme.shadows[1],
    padding: theme.spacing(2),
    transition: theme.transitions.create(['box-shadow'], {
      duration: theme.transitions.duration.enteringScreen,
      easing: theme.transitions.easing.easeInOut
    }),

    '&, &:hover': {
      backgroundColor: theme.palette.background.paper,
    },

    '&:active': {
      boxShadow: theme.shadows[5],
    },

    [theme.breakpoints.up('sm')]: {
      padding: theme.spacing(2.5),
    },
  },
  playNavBtn: {
    border: `1px solid ${theme.palette.grey[300]}`,

    '&, &:hover': {
      backgroundColor: theme.palette.background.paper,
    },

    [theme.breakpoints.up('sm')]: {
      padding: theme.spacing(2),
    },
  },
  playNavBtnTransparent: {
    border: 'none',
    backgroundColor: 'transparent',
    color: '#fff',

    '&, &:hover': {
      backgroundColor: 'transparent',
    },
  }
});

const styledEnginePlayer = withStyles(styles)(EnginePlayer);

const mapStateToProps = (state: any) => ({

})

const mapDispatchToProps = {

}

export default connect(mapStateToProps, mapDispatchToProps)(styledEnginePlayer);
