import React, { FunctionComponent, ReactElement, ReactNode } from 'react'
import clsx from 'clsx';
import { Container, makeStyles, PaperProps, SwipeableDrawer, SwipeableDrawerProps } from '@material-ui/core';

interface Props extends SwipeableDrawerProps {
  // 
}

type NewPaperProps = Partial<PaperProps>;

const BottomDrawer: FunctionComponent<Props> = ({
  children,
  ...rest
}) => {
  const classes = useStyles();

  const { PaperProps, ...restProps } = rest;
  const newPaperProps: NewPaperProps = PaperProps || {};

  newPaperProps.className = clsx(classes.paper, newPaperProps.className);

  return (
    <SwipeableDrawer {...restProps} PaperProps={newPaperProps}>
      <Container maxWidth={false} className={classes.topPull}>
        <div className={classes.topPuller}></div>
      </Container>

      <div className={classes.content}>
        {children}
      </div>
    </SwipeableDrawer>
  )
}

const useStyles = makeStyles((theme) => ({
  paper: {
    overflow: 'visible',
  },
  topPull: {
    paddingTop: theme.spacing(2),
    position: 'absolute',
    bottom: '100%',
    left: 0,
    width: '100%',

    '&:after': {
      content: '""',
      position: 'absolute',
      zIndex: -1,
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      backgroundColor: theme.palette.background.paper,
      borderTopLeftRadius: theme.spacing(1.75),
      borderTopRightRadius: theme.spacing(1.75),
    }
  },
  topPuller: {
    margin: '0 auto',
    width: '2rem',
    height: '.25rem',
    borderRadius: '.25rem',
    backgroundColor: theme.palette.grey[500],
  },
  content: {
    flexGrow: 1,
    overflow: 'auto',
    paddingTop: theme.spacing(2),
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
  }
}));

export type { Props as BottomDrawerProps };

export default BottomDrawer
