import { createSlice, PayloadAction, SliceCaseReducers } from '@reduxjs/toolkit'
import { UserInterface, UserStoreState } from '../../types/user';

export const uiReducer = createSlice<UserStoreState, SliceCaseReducers<UserStoreState>, 'user'>({
  name: 'user',
  initialState: {
    user: null,
  },
  reducers: {
    setUser: (state, action: PayloadAction<UserInterface>) => {
      return {
        ...state,
        user: action.payload,
      };
    },
  },
});

// Action creators are generated for each case reducer function
export const { setUser } = uiReducer.actions;

export default uiReducer.reducer;
